@import url(https://fonts.googleapis.com/css?family=Londrina+Solid:100,300,400,900);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  /* Show a dark background "behind" the app when scrolling past top/bottom */
  background-color: #4a4a4a;
}

html,
body,
.root,
.App,
main {
  min-height: 100vh;
}

main {
  text-align: center;
  font-family: 'Survivant', 'Londrina Solid', sans-serif;
  color: white;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

article {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

article h1 {
  color: white;
  text-shadow: -1px 0 black, 0 2px black, 2px 0 black, 0 -1px black;
  font-family: 'Survivant';
  font-size: 3rem;
  font-weight: 500;
  align-content: center;
  text-align: center;
  margin: 0;
  margin-bottom: 10px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  font-family: 'Survivant';
  src: url(/static/media/survivant.b1dda199.ttf)
}

@font-face {
  font-family: 'Survivor Font';
  src: url(/static/media/survivorfont.9ba24af1.ttf)
}
/* Dropdown content (hidden by default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  padding: 3px;
}

/* Add a grey background color to dropdown links on hover */
.dropdown-content div.season-option:hover:not(.selected) {
  text-decoration: none;
  background-color: #ddd;
}

div.season-option.selected {
  background-color: #111;
  color: white;
  text-decoration: none;
}

/* Show the dropdown menu on hover */
.dropdown.active .dropdown-content {
  display: block;
}

@media only screen and (max-width: 671px) {
  .navbar h1 {
    justify-self: center;
  }

  #season-select {
    justify-self: center;
  }

  .dropdown-content {
    position: fixed;
    left: 0; 
  }
}
/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  
  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: -5px;
  left: 105%;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
  font-family: 'Londrina Solid', sans-serif;
  font-size: 1.1rem;
  font-weight: 300;
}

